body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.flagSize {
    height: 16px;
}
.iconSizeHome {
    height: 24px;
    font-size: 24px;
}
.jumboTron {
    background-color: black;
    text-align: center;
    align-items: center;
    justify-content: center;
}
.SpImage {
    height: 500px;
}
.photoImage {
    height: 700px;
}
.interpretImage {
    height: 200px;
}
.sealImage {
    height: 200px;
}
.linkColor {
    color: black;
}
.linkColor:hover {
    color: red;
}
.titleHover:hover {
    cursor:pointer;
}
.cardImage {
    height: 200px;
    object-fit: cover;
}

.cardHover:hover {
    cursor:pointer;
}
